.wrapper {
  display: flex;
  margin-top: 10px;
  width: 125px;
  justify-content: space-between;

  p {
    align-items: center;
    display: flex;
    margin: 10px;
  }

  .resourceSelector{
    display: flex;
    position: relative;
    z-index: 9999;

    .resourceButtonSelector{
      position: absolute;
      width: 350px;
      margin-left: 80px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;

      .resourceUnit {
        position: relative;

        div {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 20px;
          height: 20px;
          position: absolute;
          z-index: 5;
          right: 7px;
          bottom: 12px;

          p {
            font-size: 10px;
            color: white;
          }
        }
      }
    }
    .resourceButtonSelectorHidden{
      display: none;
    }
    .resourceDivFactor {
      position: absolute;
      width: 20px;
      height: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      right: 5px;
      bottom: 8px;

      p {
        font-size: 10px;
        color: white;
      }
    }
  }
}

.backgroundSkeleton {
  background: linear-gradient(rgb(210, 187, 139), rgb(210, 187, 139)) center/73% 73%;
  background-repeat:no-repeat;
}
