.wrapper {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

@media (max-height: 935px) {
    .wrapper{
        height: 960px;
    }
}

@media (max-width: 1150px) {
    .wrapper{
        width: 1150px;
    }
}




