.header {
  box-sizing: border-box;
  z-index: 9999;
  top: 0;
  width: 100%;
  position: fixed;
  background-color: var(--header-color);
  color: var(--color);
  height: 65px;
  display: flex;
  justify-content: space-between;

  h1 {
    height: 100%;
    width: 220px;
    text-align: center;
    line-height: 60px;
    vertical-align: middle;
  }

  button {
    filter: drop-shadow(2px 2px 3px black);
    margin: auto 1rem auto 15px;
    border-radius: 5px;
    min-width: 50px;
    height: 21px;
  }
}

.headerLink{
  text-decoration: none;
  position: absolute;
  left: 50%;
  transform: translate(-50%,0);
}

.headerLink:hover{
  h1 {
    scale: 1.1;
  }
}

.headerTextStyles:after{
  color: rgb(242,184,59);
  content: 'Albion Toolkit';
}

@media (max-width: 1100px) {
  .headerLink{
    margin-left: 0;
  }
}

@media (max-width: 550px) {
  .headerTextStyles:after{
    content: 'AToolkit';
  }

}

